import * as L from 'leaflet';
import 'leaflet-fullscreen';
import { Comic, Page, Panel } from '@/model';
import apiService from '@/services/ApiService2';

const CRSPixel = L.Util.extend(L.CRS.Simple, {
  transformation: new L.Transformation(1, 0, 1, 0),
});

export class PageViewer extends L.Map {
  private pageImage?: L.ImageOverlay;
  private comic?: Comic;
  private page?: Page;
  private pageNumber = 0;
  private imageCache: Promise<string>[] = [];

	constructor(element: string | HTMLElement, options?: L.MapOptions) {
    super(element, options);

    this.on('click', (e: L.LeafletMouseEvent) => {
      this.page!.panels.forEach((panel, panelIndex) => {
        const panelBounds = this.getPanelBounds(panel);
        if (panelBounds.contains(e.latlng)) {
          let selectedPanel = panelIndex;

          if (this.isPanelCentered(panel)) {
            if (e.latlng.lng > this.getCenter().lng) {
              selectedPanel++;
            } else {
              selectedPanel--;
            }
          }

          if (selectedPanel < 0 && this.pageNumber > 0) {
            this.loadPage(this.pageNumber - 1);
          } else if (selectedPanel >= this.page!.panels.length && this.pageNumber < this.comic!.pages.length - 1) {
            this.loadPage(this.pageNumber + 1);
          } else {
            this.zoomToPanel(selectedPanel);
          }

          return;
        }
      });
    });
  }

  public async loadComic(comic: Comic): Promise<void> {
    this.comic = comic;
    this.imageCache = Array<Promise<string>>(this.comic.pages.length);
    await this.loadPage(0);
  }

  public async loadPage(pageNumber: number): Promise<void> {
    this.page = this.comic!.pages[pageNumber];
    this.pageNumber = pageNumber;

    const imageUrl = await this.preloadPage(pageNumber);

    if (pageNumber > 0) {
      this.preloadPage(this.pageNumber - 1);
    }

    if (pageNumber < this.comic!.pages.length - 1) {
      this.preloadPage(this.pageNumber + 1);
    }

    const bounds = L.latLngBounds([0, 0], [this.page.height, this.page.width]);

    if (this.pageImage) {
      this.pageImage.setUrl(imageUrl);
      this.pageImage.setBounds(bounds);
    } else {
      this.pageImage = L.imageOverlay(imageUrl, bounds).addTo(this);
      this.pageImage.on('load', (e) => {
        this.fitBounds(this.pageImage!.getBounds());
      });
    }
  }

  private async preloadPage(pageNumber: number): Promise<string> {

    if (this.imageCache[pageNumber]) {
      return this.imageCache[pageNumber];
    }

    const url = this.comic!.pages[pageNumber].url;
    return this.imageCache[pageNumber] = apiService.downloadPage(this.comic!, pageNumber);
  }

  public zoomToPanel(panelIndex: number) {
    const panel = this.page!.panels[panelIndex];
    this.flyToBounds(this.getPanelBounds(panel));
  }

  private getPanelBounds(panel: Panel): L.LatLngBounds {
    return L.latLngBounds([panel.y, panel.x], [panel.y + panel.height, panel.x + panel.width]);
  }

  private getDistance(a: L.LatLng, b: L.LatLng): number {
    return Math.sqrt((a.lat - b.lat) * (a.lat - b.lat) + (a.lng - b.lng) * (a.lng - b.lng));
  }

  private isPanelCentered(panel: Panel): boolean {
    const panelBounds = this.getPanelBounds(panel);
    const distance = this.getDistance(panelBounds.getCenter(), this.getCenter());

    if (distance < 10 && this.getBounds().contains(panelBounds)) {
      const boundsWidth = Math.abs(this.getBounds().getEast() - this.getBounds().getWest());
      const boundsHeight = Math.abs(this.getBounds().getNorth() - this.getBounds().getSouth());
      const ratio = Math.max(panel.width / boundsWidth, panel.height / boundsHeight);

      return distance < 10 && ratio > 0.8;
    }

    return false;

  }
}

PageViewer.prototype.options = L.Util.extend(
	{},
	(L.Map as any).prototype.options,
	{
    attributionControl: false,
    crs: CRSPixel,
    center: [0, 0],
    zoom: -1,
    minZoom: -5,
    maxZoom: 5,
    zoomSnap: 0.1,
    zoomControl: false,
    fullscreenControl: true,
	}
);
