









import { Component, Vue } from 'vue-property-decorator';
import axios from 'axios';
import * as L from 'leaflet';
import 'leaflet-draw';
import 'leaflet-easybutton';
import { Comic, Panel } from '@/model';

declare module 'leaflet' {
  interface Rectangle {
    panel: Panel;
  }
}

@Component
export default class PageEditor extends Vue {
  private map: L.Map | null = null;
  private pagesFeature: L.FeatureGroup = new L.FeatureGroup();

  private comic: Comic | null = null;
  private comicId = '';
  private pageNumber = 0;

  async mounted() {
    this.comicId = this.$route.params.comicId;
    this.pageNumber = Number(this.$route.params.pageNumber);

    const comicResponse = await axios.create().get<Comic>(`/api/comic/${this.comicId}`);
    this.comic = comicResponse.data;
    const page = this.comic!.pages[this.pageNumber];

    this.reloadPanels(page.panels);

    const CRSPixel = L.Util.extend(L.CRS.Simple, {
      transformation: new L.Transformation(1, 0, 1, 0),
    });

    const mapElement = this.$refs.map as HTMLElement;

    this.map = L.map(
      mapElement,
      {
        center: [0, 0],
        zoom: -1,
        crs: CRSPixel,
        minZoom: -100,
        maxZoom: 200,
        zoomSnap: 0.1,
      },
    );

    if (this.pagesFeature.getLayers().length) {
      this.map.fitBounds(this.pagesFeature.getBounds());
    }

    this.map.on(L.Draw.Event.CREATED, (e) => {
      const rectangle: L.Rectangle = e.layer;
      rectangle.panel = {} as Panel;
      this.pagesFeature.addLayer(rectangle);
      this.savePanels();
    });

    this.map.on(L.Draw.Event.EDITED, (e) => {
      this.savePanels();
    });

    this.map.on(L.Draw.Event.DELETED, (e) => {
      this.savePanels();
    });

  this.map.addLayer(this.pagesFeature);

    this.map.addControl(new L.Control.Draw({
      edit: {
        featureGroup: this.pagesFeature,
      },
      draw: {
        polyline: false,
        polygon: false,
        circle: false,
        circlemarker: false,
        marker: false,
      }
    }));

    const pageImage = L.imageOverlay(this.comic.pages[this.pageNumber].url, [[0, 0], [page.height, page.width]]);
    pageImage.addTo(this.map);
  }

  private updatePanel(rectangle: L.Rectangle): void {
      rectangle.panel.x = Math.round(rectangle.getBounds().getWest());
      rectangle.panel.y = Math.round(rectangle.getBounds().getSouth());
      rectangle.panel.width = Math.round(rectangle.getBounds().getEast() - rectangle.getBounds().getWest());
      rectangle.panel.height = Math.round(rectangle.getBounds().getNorth() - rectangle.getBounds().getSouth());
  }

  private panelToRectangle(panel: Panel): L.Rectangle {
    const rectangle = new L.Rectangle([[panel.y, panel.x], [panel.y + panel.height, panel.x + panel.width]]);
    rectangle.panel = panel;
    return rectangle;
  }

  private reloadPanels(panels: Panel[]): void {
    this.pagesFeature.clearLayers();

    if (panels) {
      panels.forEach((panel, i) => {
        const rect = this.panelToRectangle(panel);
        rect.setTooltipContent(i.toString());
        rect.bindTooltip(i.toString());
        rect.addTo(this.pagesFeature);
      });
    }
  }

  private async savePanels(): Promise<void> {
    const panels: Panel[] = this.pagesFeature.getLayers().map(rectangle => {
      this.updatePanel(rectangle as L.Rectangle);
      return (rectangle as L.Rectangle).panel
    });

    const response = await axios.create().post<Panel[]>(`/api/comic/${this.comicId}/${this.pageNumber}/panels`, panels);
    this.reloadPanels(response.data);
  }

  private async autoDetectPanels(): Promise<void> {
    const response = await axios.create().get<Panel[]>(`/api/comic/${this.comicId}/${this.pageNumber}/panels/autodetect`);
    this.reloadPanels(response.data);
  }
}
