







import { Component, Vue } from 'vue-property-decorator';

@Component
export default class App extends Vue {
  async mounted() {
    this.loadKey();
  }

  private loadKey() {
    const key = this.$route.query.key as string;

    if (key) {
      sessionStorage.setItem('key', key);
      this.$router.replace({
        query: {
          key: undefined,
        }
      })
    } else {
      sessionStorage.removeItem('key');
    }
	}
}
