import axios from 'axios';
import base64 from 'base64-arraybuffer';
import { Comic, Page } from '@/model';

const axiosInstance = axios.create();

class ApiService {
  public async getComic(id: string): Promise<Comic> {

      const comicResponse = await axiosInstance.get<Comic>(`/data/comic/${id}/data.json`, {
        responseType: 'json',
      });

      return await comicResponse.data;
    }

  public async downloadPage(comic: Comic, pageNumber: number): Promise<string> {
    const page = comic.pages[pageNumber];
    const response = axiosInstance.get<ArrayBuffer>(page.url, {
      responseType: 'arraybuffer',
    });

    const header = base64.decode(comic.hash);

    const body = await response;

    const blob = new Blob([header, body.data], {
      type: 'image/jpg',
    });
    debugger;
    return URL.createObjectURL(blob);
  }
}

export default new ApiService();
